import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { Observable } from 'rxjs';
import { Thread, Collections } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';

@IonicPage({
  segment: 'foro/:id'
})
@Component({
  selector: 'page-foro-detail',
  templateUrl: 'foro-detail.html',
})
export class ForoDetailPage implements OnInit {

  public thread$: Observable<Thread[]>

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
    private modal: ModalController,
  ) {
  }

  ngOnInit() {

  }

  ionViewDidLoad() {
    const id = this.navParams.get('id')
    this.thread$ = this.afs.collection<Thread>(Collections.THREAD, ref => ref.where('cat_id', '==', id)).valueChanges()
  }

  openAddThread() {
    const m = this.modal.create('ThreadAddPage', { cat: this.navParams.get('id') });
    m.present();
  }

}



